.score {
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  width: 200px;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%);
  p {
    color: azure;
    font-size: 40px;
    font-weight: bold;
    line-height: 50px;
    text-align: center;
  }
}

#winText {
  text-align: center;
  display: none;
  background: #000000;
  position: absolute;
  top: 10%;
  width: 100vw;
  height: 80px;
  p {
    color: aquamarine;
    font-size: 40px;
    align-items: center;
    text-align: center;
  }
}
